const HOST = require('../../config/common').getHost() // 
const _axios = require('axios') // axios
const Qs = require('qs')
const axios = _axios.create({ // 
  baseURL: HOST, // IP
  timeout: 5000 // 
})
// 
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
/**
 * [description]
 * @param  {[type]} url     []
 * @param  {[type]} params  [, url]
 * @param  {[type]} headers []
 * @param  {[type]} timeout []
 * @return {[type]}         [promise]
 */
module.exports.get = function ({ url = '', params = {}, headers = {}, timeout = 5000, responseType }) {
  const promise = axios.get(url, { params, timeout, responseType })
  return promise
}
/**
 * [POST]
 * @param  {[type]} url     []
 * @param  {[type]} data    []
 * @param  {[type]} params  []
 * @param  {[type]} headers []
 * @param  {[type]} timeout []
 * @return {[type]}         [promise]
 */
module.exports.post = function ({ url = '', data = {}, params = {}, headers = {}, timeout = 5000 }) {
  const promise = axios.post(url, null, {
    transformRequest: function (data) {
      return Qs.stringify(data, { arrayFormat: 'brackets' })
    },
    data,
    params,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    timeout
  })
  return promise
}
/**
 * [PUT]
 * @param  {[type]} url     []
 * @param  {[type]} data    []
 * @param  {[type]} params  []
 * @param  {[type]} headers []
 * @param  {[type]} timeout []
 * @return {[type]}         [promise]
 */
module.exports.put = function ({ url = '', data = {}, params = {}, headers = {}, timeout = 5000 }) {
  const promise = axios.put(url, { data, params, headers, timeout })
  return promise
}
/**
 * [DELETE]
 * @param  {[type]} url     []
 * @param  {[type]} data    []
 * @param  {[type]} params  []
 * @param  {[type]} headers []
 * @param  {[type]} timeout []
 * @return {[type]}         [promise]
 */
module.exports._delete = function ({ url = '', data = {}, params = {}, headers = {}, timeout = 5000 }) {
  const promise = axios.delete(url, { data, params, headers, timeout })
  return promise
}
